@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
  }
  
  .switch input { 
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: .2s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: .2s;
  }
  
  input:checked + .slider {
    background-color: #2196F3;
  }
  
  input:focus + .slider {
    box-shadow: 0 0 1px #2196F3;
  }
  
  input:checked + .slider:before {
    transform: translateX(26px);
  }
  
  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  }
body {
  font-family: "Ubuntu", sans-serif;
  background-color: #dbdbdb;
}

#root {
  display: flex;
  align-items: center;
  justify-content: center;
}

#content {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  text-align: center;
  padding: 15px;
}

.menu {
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin-top: 15px;
  background-color: #fdfdfd;
  width: 700px;
  border-radius: 5px;
  box-shadow: 2px 2px 4px 0 black;
}

.menu-item {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
}

#page {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  background-color: #fdfdfd;
  text-align: left;
  width: 700px;
  margin: 20px;
  border-radius: 5px;
  box-shadow: 2px 2px 4px 0 black;
}

#page input {
  font-family: "Ubuntu", sans-serif;
  background-color: #d7edff;
  font-size: 16px;
  padding: 3px;
  width: 180px;
  border: none;
  margin: 5px;
  border-radius: 3px;
  box-shadow: 0 0 0 1px #757575;
}

#page input:focus {
  outline: none;
  box-shadow: 0 0 2px 1px black;
}

.input-wrap {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.label {
  font-size: 13px;
  margin: 0 0 2px 0;
}

#general {
  background-color: #b0d9ff;
  width: 100%;
  height: 120px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin: 20px 0 10px 0;
  border-top: 1px solid black;
  border-bottom: 1px solid black;
}

#profile-photo {
  width: 140px;
  height: 140px;
}

#education {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  margin: 10px 0 10px 0;
}

#experience {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  margin: 10px 0 10px 0;
}

.print-btn {
  font-family: "Ubuntu", sans-serif;
  font-size: 18px;
  background-color: #b0d9ff;
  border: none;
  width: 180px;
  height: 40px;
  padding: 5px;
  margin: 5px;
  border-radius: 3px;
  align-self: center;
  justify-self: center;
  box-shadow: 2px 2px 4px 0 black;
  transition: all 0.2s;
}

.print-btn:hover {
  background-color: #2586e0;
  color: white;
}

.print-btn:active {
  box-shadow: 1px 1px 2px 0 black;
}

.print-btn:disabled {
  background-color: #b0d9ff;
  color: #949494;
  box-shadow: 1px 1px 2px 0 black;
}

